<template>
  <b-container fluid id="abs-nav">
    <b-row>
      <b-col cols="2" sm="2" md="2" lg="2" xl="2" class="left" ref="nav">
        <div class="fill-space">
          <!-- <img src="../assets/logo.png" alt> -->
        </div>
        <b-list-group>
          <b-list-group-item button @click="moveTo('home')">HOME</b-list-group-item>
          <b-list-group-item button @click="moveTo('works')">WORKS</b-list-group-item>
          <b-list-group-item button @click="moveTo('about_me')">MYSELF</b-list-group-item>
          <b-list-group-item button @click="moveTo('contact_me')">CONTACT</b-list-group-item>
        </b-list-group>
        <div class="fill-space"></div>
      </b-col>
    </b-row>
  </b-container>
</template>
<script>

// 判断坐标是否过半
function isPassHalf(current,half,end){
  // 都在中间点左侧或右侧则已经过半
  if((half - current) > 0 && (half - end) > 0 || (half - current) < 0 && (half - end) < 0){
    // console.log('当前',current,'半程',half,'结束',end);
    return true;
  }else{
    return false;
  }
}
// 判断是否到达指定位置
function isArrived(current,half,end){
  // console.log('当前',current,'半程',half,'目标',end);//用于测试位置
  
if(Math.abs(current - end) < 1){
    return true;
  }else if(( (end - half > 0)&&(current - end) > 0 ) || ((half - end) > 0)&&(end - current) > 0){//当半程与当前位置不在同侧时，必将到达终点
    return true;
  }else{
    return false;
  }
}
// 兼容方法，获取浏览器scrollTop
function getScrollTop(){
  // console.log('chrome浏览器',document.documentElement.scrollTop);
  // console.log('ie浏览器',document.body.scrollTop);
  // window.pageYOffset不能放在最后，因为其返回值为undefined
  return document.documentElement.scrollTop || window.pageYOffset || document.body.scrollTop;
}
// 设置浏览器scrollTop的兼容性方法
function setScrollTop(scrollTop){
  if(document.documentElement.scrollTop ||  document.documentElement.scrollTop == 0){
    document.documentElement.scrollTop = scrollTop;
  }
  if(document.body.scrollTop || document.body.scrollTop == 0){
    // console.log('ie 设置',scrollTop);
    document.body.scrollTop = scrollTop;
  }
  if(window.pageYOffset || window.pageYOffset == 0){
    window.pageYOffset = scrollTop;
  }
}

function animation($this) {

  
  let a = 25; 

  let currentY = getScrollTop();
  if(isPassHalf(currentY,$this.halfEndY,$this.endY)) {
    $this.currentSpeed -= a*(1/100);
    if($this.currentSpeed< 1){
      $this.currentSpeed = 1;
    }
  }else{
    $this.currentSpeed += a*(1/100);

  }
  
  // console.log('speed:',$this.currentSpeed);//用于测试速度
  
  // 运行方向 boolean
  let direction = $this.endY - $this.halfEndY > 0;
  
  
  if(direction){
    if($this.currentSpeed > 6){
      setScrollTop(currentY + $this.currentSpeed * 3);
    }else{
      setScrollTop(currentY + $this.currentSpeed);
    }
  }else{
    if($this.currentSpeed > 6){
      setScrollTop(currentY - $this.currentSpeed * 3);
    }else{
      setScrollTop(currentY - $this.currentSpeed);
    }
    
  }
  // console.log('是否抵达',isArrived(currentY,$this.halfEndY,$this.endY,$this.currentSpeed));
  // 当到达或到达附近时
  let timer;
  if(isArrived(currentY,$this.halfEndY,$this.endY)){
    setScrollTop($this.endY);
    // 更改全局变量为可滑动状态
    $this.$store.commit({
            type:'pageScroll',
            scrollstate: false,
          })
    $this.time = 0;
    $this.currentSpeed  = 0;
    if(timer){
      clearTimeout(timer);
    }
  }
  
  if(!timer){
    timer = setTimeout(()=>{
      // console.log('方法内部',$this.$store.state.is_scroll);
      
      if($this.$store.state.is_scroll){
          animation($this)//定时器暂时取消
      }
    },1000/60);
  }
}

export default {
  mounted() {
    window.addEventListener('scroll',()=>{
      // console.log(document.documentElement.scrollTop);
      this.$data.docY = document.documentElement.scrollTop;
      
    })
  },
  data: function() {
    return {
      endY:0,
      halfEndY:0,
      time:0,
      currentSpeed: 0,
      isScroll: false,
    };
  },
  methods: {
    moveTo(ref) {
      if (document.documentElement) {
        // 当处于滑动状态时 取消坐标更新

        // this.$store.state.isScroll //全局运动状态
        
                if(!this.$store.state.is_scroll){
          
          // 目标点纵向坐标
          this.$data.endY = getScrollTop() + this.$store.state[ref].getBoundingClientRect().top;

          this.$data.halfEndY =getScrollTop() + (this.$store.state[ref].getBoundingClientRect().top/2);

           this.$store.commit({
            type:'pageScroll',
            scrollstate: true,
          })
          
          if(this.$store.state.is_scroll){
            
            animation(this);
          }else{
            //滚动出错时调试此处监视异常状态 当组件错误修改参数时调用
            // console.log('全局变量设定出错');
          }
        
        } 
      }
    }
  }
};
</script>

