<template>
  <!-- cols sm 条件下为两行 md lg xl为一行 -->
  <b-container fluid id="top_menu" ref="home">
    <b-row>
      <b-col col sm="12" cols="12" md="6" class="left"
        @mouseenter="enterHandler('left')"
        @mouseleave="leaveHandler('left')">
           
            <div class="full" >
              <!-- <img src="../assets/logo.png" alt="" id="logo"> -->
                <transition name="slide-fade">
                    <h1 class="menu_title" v-if="mouseEnterLeft">Soda's Home Page</h1>   
                </transition>
                <transition name="fade">
                    <b-list-group class="item_center menu_nav" v-if="mouseEnterLeft">
                        <b-list-group-item button @click="moveTo('works')">WORKS</b-list-group-item>
                        <b-list-group-item button @click="moveTo('about_me')">MYSELF</b-list-group-item>
                        <b-list-group-item button @click="moveTo('contact_me')">CONTACT</b-list-group-item>
                    </b-list-group>
                </transition>

                <transition name="fade">        
                    <div class="item_center" v-if="!mouseEnterLeft">
                      <svg class="icon" aria-hidden="true">
                        <use xlink:href="#soda-liebiaomoshi_kuai"></use>
                      </svg> 
                    </div>
                </transition>
                
            </div>
             
        </b-col>
      <b-col col sm="12" cols="12" md="6" class="right"
        @mouseenter="enterHandler('right')"
        @mouseleave="leaveHandler('right')">
            <div class="full">
                <img src="../assets/desktop.jpg" alt="">
                <div class="mask"></div>

                <transition name="fade">        
                    <div class="item_center" v-if="mouseEnterRight" @click="moveTo('works')"><b-button>WORKS</b-button> </div>
                </transition>

                <transition name="fade">        
                    <div class="item_center" v-if="!mouseEnterRight">
                      <svg class="icon white-icon" aria-hidden="true">
                        <use xlink:href="#soda-jiantouxia"></use>
                      </svg> 
                    </div>
                </transition>

            </div>
      </b-col>
    </b-row>
  </b-container>
</template>

<script>
// 判断坐标是否过半
function isPassHalf(current,half,end){
  // 都在中间点左侧或右侧则已经过半
  if((half - current) > 0 && (half - end) > 0 || (half - current) < 0 && (half - end) < 0){
    // console.log('当前',current,'半程',half,'结束',end);
    return true;
  }else{
    return false;
  }
}
// 判断是否到达指定位置
function isArrived(current,half,end){
  // console.log('当前',current,'半程',half,'目标',end);//用于测试位置
  
if(Math.abs(current - end) < 1){
    return true;
  }else if(( (end - half > 0)&&(current - end) > 0 ) || ((half - end) > 0)&&(end - current) > 0){//当半程与当前位置不在同侧时，必将到达终点
    return true;
  }else{
    return false;
  }
}
// 兼容方法，获取浏览器scrollTop
function getScrollTop(){
  // console.log('chrome浏览器',document.documentElement.scrollTop);
  // console.log('ie浏览器',document.body.scrollTop);
  // window.pageYOffset不能放在最后，因为其返回值为undefined
  return document.documentElement.scrollTop || window.pageYOffset || document.body.scrollTop;
}
// 设置浏览器scrollTop的兼容性方法
function setScrollTop(scrollTop){
  // console.log('set:',scrollTop);
  
  if(document.documentElement.scrollTop ||  document.documentElement.scrollTop == 0){
    document.documentElement.scrollTop = scrollTop;
  }
  if(document.body.scrollTop || document.body.scrollTop == 0){
    // console.log('ie 设置',scrollTop);
    document.body.scrollTop = scrollTop;
  }
  if(window.pageYOffset || window.pageYOffset == 0){
    window.pageYOffset = scrollTop;
  }
}

function animation($this) {

  
  let a = 25; 

  let currentY = getScrollTop();
  if(isPassHalf(currentY,$this.halfEndY,$this.endY)) {
    $this.currentSpeed -= a*(1/100);
    if($this.currentSpeed< 1){
      $this.currentSpeed = 1;
    }
  }else{
    $this.currentSpeed += a*(1/100);

  }
  
  // console.log('speed:',$this.currentSpeed);//用于测试速度
  
  // 运行方向 boolean
  let direction = $this.endY - $this.halfEndY > 0;
  
  
  if(direction){
    if($this.currentSpeed > 6){
      setScrollTop(currentY + $this.currentSpeed * 3);
    }else{
      setScrollTop(currentY + $this.currentSpeed);
    }
  }else{
    if($this.currentSpeed > 6){
      setScrollTop(currentY - $this.currentSpeed * 3);
    }else{
      setScrollTop(currentY - $this.currentSpeed);
    }
    
  }
  // console.log('是否抵达',isArrived(currentY,$this.halfEndY,$this.endY,$this.currentSpeed));
  // 当到达或到达附近时
  let timer;
  if(isArrived(currentY,$this.halfEndY,$this.endY)){
    setScrollTop($this.endY);
    // 更改全局变量为可滑动状态
    $this.$store.commit({
            type:'pageScroll',
            scrollstate: false,
          })
    $this.time = 0;
    $this.currentSpeed  = 0;
    if(timer){
      clearTimeout(timer);
    }
  }
  
  if(!timer){
    timer = setTimeout(()=>{
      // console.log('方法内部',$this.$store.state.is_scroll);
      
      if($this.$store.state.is_scroll){
          animation($this)//定时器暂时取消
      }
    },1000/60);
  }
}
export default {
  mounted() {
    this.$store.commit({
      type: "saveHome",
      obj: this.$refs.home
    });
  },
  data: function() {
    return {
      mouseEnterLeft: false,
      mouseEnterRight: false,
      endY:0,
      halfEndY:0,
      time:0,
      currentSpeed: 0,
      isScroll: false,
    };
  },
  methods: {
    //   通过处理鼠标进入事件更改hover的逻辑属性
    enterHandler(which) {
      if ("left" === which) {
        this.mouseEnterLeft = true;
      } else if ("right" === which) {
        this.mouseEnterRight = true;
      }
    },
    leaveHandler(which) {
      if ("left" === which) {
        this.mouseEnterLeft = false;
      } else if ("right" === which) {
        this.mouseEnterRight = false;
      }
    },
    moveTo(ref) {
      if (document.documentElement) {
        // 当处于滑动状态时 取消坐标更新
        // console.log(getScrollTop());
        
        if(!this.$store.state.is_scroll){
          
          // 目标点纵向坐标
          this.$data.endY = getScrollTop() + this.$store.state[ref].getBoundingClientRect().top;

          this.$data.halfEndY =getScrollTop() + (this.$store.state[ref].getBoundingClientRect().top/2);

           this.$store.commit({
            type:'pageScroll',
            scrollstate: true,
          })
          
          if(this.$store.state.is_scroll){
            
            animation(this);
          }else{
            //滚动出错时调试此处监视异常状态 当组件错误修改参数时调用
            // console.log('全局变量设定出错');
          }
        }  
      }
    }
  }
};
</script>


<style scoped>
    .left,.right{
        padding: 0;
        z-index: 1;
    }
    .left{
        background-color: whitesmoke;
        
    }
    .right img{
        width: auto;
        min-width: auto;
        height: 100%;
        min-height: 500px;
        position: absolute;
        left: 0;
        bottom: 0;
        z-index: 0;
    }
    .right{
      
      position: relative;
    }

    .full{
        width: 100%;
        height: 100vh;
        position: relative;
        overflow: hidden;
    }
    .mask{
      width: 100%;
      height: 100vh;
      background-color: rgba(21,21,21, .97);
      position: absolute;
      top: 0;
      left: 0;
    }
   
    .item_center{
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
    }
    .menu_nav{
        width: 100%;
    }
    .menu_nav .list-group-item{
        text-align: center;
    }
    .menu_title{
        position: absolute;
        top: 15%;
        left: 50%;
        transform: translate(-50%,-50%);
    }
    .icon {
    width: 2em;
    height: 2em;
    vertical-align: -0.15em;
    fill: currentColor;
    overflow: hidden;
  }
    .white-icon{
      color: antiquewhite;
  }
/* 滑动特效 */
    .slide-fade-enter-active {
        transition: all .3s ease;
    }
    .slide-fade-leave-active {
        transition: all .4s cubic-bezier(1.0, 0.5, 0.8, 1.0);
    }
    .slide-fade-enter, .slide-fade-leave-to
        /* .slide-fade-leave-active for below version 2.1.8 */ {
        transform: translateY(10px);
        opacity: 0;
    }
    /* 显示隐藏特效 */
    .fade-enter-active, .fade-leave-active {
        transition: opacity .5s;
        
    }
    .fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
        opacity: 0;
    }
</style>
