<script>
export default {
  mounted() {
    // 判断$refs.contentWidth控件是否宽度过小
    function isFullScreen() {
      if (document.body && $this.$refs && $this.$refs.contentWidth) {
        if (
          $this.$refs.contentWidth.clientWidth / document.body.clientWidth >
          0.99
        ) {
          if (!$this.isFull) $this.isFull = true;
        } else {
          if ($this.isFull) $this.isFull = false;
        }
      }
    }
    let $this = this;
    isFullScreen();
    // 动态更新组件全屏信息
    window.addEventListener("resize", () => {
      isFullScreen();
    });
    // 将组件锚点存入vuex
    // console.log('存入',this.$refs.works);

    this.$store.commit({
      type: "saveWorks",
      obj: this.$refs.works
    });
    this.$store.commit({
      type: "saveAboutMe",
      obj: this.$refs.about_me
    });
    this.$store.commit({
      type: "saveContactMe",
      obj: this.$refs.contact_me
    });
  },
  data: function() {
    return {
      isFull: false,
      initScreenWidth: document.body.clientWidth
    };
  },
  computed: {
    topNavStyle() {
      if (!this.isFull) {
        return { display: "none" };
      }
      return { display: "block" };
    }
  }
};
</script>


