import Vue from 'vue'
import App from './App.vue'
import BootstrapVue from 'bootstrap-vue'
import Vuex from 'vuex'
import './assets/icon/iconfont'

Vue.use(Vuex)
Vue.use(BootstrapVue)

import 'bootstrap/dist/css/bootstrap.css'
import 'bootstrap-vue/dist/bootstrap-vue.css'

Vue.config.productionTip = false


const store = new Vuex.Store({
  state:{
    count: 0,
    home:{},
    works:{},
    about_me:{},
    contact_me:{},
    is_scroll: false,
  },
  mutations: {
    saveHome(state,params){
      state.home = params.obj;
    },
    saveWorks(state,params){
      // console.log('存入',params.obj);
      state.works = params.obj;
    },
    saveAboutMe(state,params){
      state.about_me = params.obj;
    },
    saveContactMe(state,params){
      state.contact_me = params.obj;
    },
    pageScroll(state,params){
      // console.log('设定为',params.scrollstate);
      
      state.is_scroll = params.scrollstate;
      // console.log(state.is_scroll);
      
    }
  }
})

new Vue({
  store:store,
  render: h => h(App),
}).$mount('#app')


